* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    align-items: stretch;
}


.nav ul {
    padding: 0;
    margin: 0;
    display: flex;
    gap: 1rem;
    list-style: none;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    padding: .25rem;
    font-size: 30px;
}

.nav li:active {
    background-color: #555;
}

.nav li:hover{
    background-color: #777;
    cursor: pointer;
}
.project_container{
    border:solid 2px;
    border-color: black;
    width:60%;
    margin-top: 50px;
    margin-left: 35%;
    display: block;
    margin-bottom: 20px;
}

.title{
    border:solid 2px;
    border-color: black;
    width:350px;
    margin:10px;
}

.info{
    display:inline-block;
    width:200px;
    padding: 30px;
}
.info_container{
    text-align: right;
    margin-right:50px;
    
}

